(function() {
    'use strict';

    angular
        .module('ghecko')
        .service('NotificationService', NotificationService);
    function NotificationService($http,API) {
        var public_api = {
            addNotification : addNotification,
            getNotifications: getNotifications
        };
        return public_api;

        function getNotifications(id){
            return $http.get(API.url + '/normal-users/'+ id + '/notification');
        }

        function addNotification(notification) {
            return $http.post(API.url + '/normal-users/'+ notification.userIdOwner + '/notification',notification);
        }

    }
})();

