(function() {
  'use strict';

  angular
    .module('ghecko')
    .controller('NotificationsController', NotificationsController);


  function NotificationsController(NotificationService) {
    var vm = this;
    vm.myId = localStorage.getItem('userId');
    function init(){
      NotificationService.getNotifications(vm.myId)
          .success(function(data){
            vm.notifications = data;
            likes();
            comments();
          });
    }
    init();

    function likes(){
      var temp = [];
      for(var i = 0; i < vm.notifications.length; i++){
        if(vm.notifications[i].type == 'like'){
          temp.push(vm.notifications[i]);
        }
      }
      vm.likes = temp;
    }

    function comments(){
      var temp = [];
      for(var i = 0; i < vm.notifications.length; i++){
        if(vm.notifications[i].type == 'comment'){
          temp.push(vm.notifications[i]);
        }
      }
      vm.comments = temp;
    }

  }
})();

(function() {
  'use strict';

  angular
    .module('ghecko')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment,$rootScope,$state) {
      var vm = this;

      vm.isloggin = localStorage.getItem('userId');
      if(vm.isloggin){
        vm.userId = localStorage.getItem('userId');
      }

      $rootScope.$on('isLogged',function(){
          vm.userId = localStorage.getItem('userId');
        });
      vm.logout = function () {
        localStorage.clear();
        $state.go('login');
      }

      //vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();


