(function () {
    'use strict';

    angular
        .module('ghecko')
        .controller('RegisterController', RegisterController);

    function RegisterController(RegisterService, $state) {
        var vm = this;

        vm.createUser = function () {
            vm.event = false;
            vm.load = true;
            var newUser =
            {
                "username": vm.username,
                "phone": vm.phone,
                "email": vm.email,
                "password": vm.password,
                "profilephoto": "http://res.cloudinary.com/ddlrxurxs/image/upload/v1478996005/frog_yuhdev.png",
                "cantseguidores": 0,
                "cantseguidos": 0,
                "cantpublicaciones": 0,
                "followers": [],
                "notifications": [],
                "name": "",
                "quote":""
            }
            RegisterService.register(newUser)
                .success(function (data) {
                    $state.go('login');
                }).error(function (data) {
                vm.event = true;
                vm.load = false;
            });
        }
    }


})();








