(function () {
    'use strict';

    angular
        .module('ghecko')
        .controller('HomeFeedController', HomeFeedController);


    function HomeFeedController(HomeService,NotificationService,$mdToast,$timeout,$rootScope) {
        var vm = this;
        vm.isLocated = false;

        function init() {
            HomeService.getPosts()
                .success(function (data) {
                    buildPosts(data)
                });
            navigator.geolocation.getCurrentPosition(showPosition);

        }

        init();

        function showPosition(position) {
            vm.isLocated = true;
            vm.lat = position.coords.latitude;
            vm.lng = position.coords.longitude;
            $timeout(function(){
                $rootScope.$apply();
            })

        }

        function buildPosts(posts) {
            for (var i = 0; i < posts.length; i++) {
                posts[i].customDate = moment(posts[i].date).fromNow();
                for (var j = 0; j < posts[i].userlikes.length; j++) {
                    if (posts[i].userlikes[j] == localStorage.getItem('userId')) {
                        posts[i].isLiked = true;
                    }

                }
            }
            for (var i = 0; i < posts.length; i++) {
                for (var x = 0; x < posts[i].comments.length; x++) {
                    posts[i].comments[x].customDate = moment(posts[i].comments[x].date).fromNow();

                }
            }

            vm.posts = posts.reverse();
        }

        function sendNotification(notification){

            NotificationService.addNotification(notification)
                .success(function(data){});
        }
        vm.addLike = function (post) {

            if (post.isLiked == true) {
                post.likes = post.likes - 1;
                for (var i = 0; i < post.userlikes.length; i++) {
                    if (post.userlikes[i] == localStorage.getItem('userId')) {
                        post.userlikes.splice(i, 1);
                    }
                }
                post.isLiked = false;
                HomeService.addLike(post)
                    .success(function (data) {
                        init();
                        $mdToast.show(
                            $mdToast.simple('You dont like it :(!')
                                .position('top', 'left')
                                .hideDelay(3000)
                        );
                    })

            } else {
                var notification  = {
                    idPost: post.id,
                    idusersend: localStorage.getItem('userId'),
                    usernamesend: localStorage.getItem('username'),
                    userphotosend: localStorage.getItem('profilephoto'),
                    type:'like',
                    userIdOwner:post.iduser
                }
                sendNotification(notification);

                post.likes = post.likes + 1;
                post.userlikes.push(localStorage.getItem('userId'));
                HomeService.addLike(post)
                    .success(function (data) {
                        init();
                        $mdToast.show(
                            $mdToast.simple('You like it :(!')
                                .position('top', 'left')
                                .hideDelay(3000)
                        );
                    })
            }

        }

        vm.send = function (pcomment, post) {

            var notification  = {
                idPost: post.id,
                idusersend: localStorage.getItem('userId'),
                usernamesend: localStorage.getItem('username'),
                userphotosend: localStorage.getItem('profilephoto'),
                type:'comment',
                userIdOwner:post.iduser
            }
            sendNotification(notification);

            var comment = {
                comment: pcomment,
                username: localStorage.getItem('username'),
                userphoto: localStorage.getItem('profilephoto'),
                date: new Date()
            }
            post.comments.push(comment);
            HomeService.addLike(post)
                .success(function (data) {
                    vm.postComment = null;
                    init();
                    $mdToast.show(
                        $mdToast.simple('Send comment!')
                            .position('top', 'left')
                            .hideDelay(3000)
                    );

                })
        }


    }
})();

