(function() {
  'use strict';

  angular
    .module('ghecko')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('ghecko')
    .directive('footerBrand', footerBrand);

  /** @ngInject */
  function footerBrand() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/footer/footer.html',
      scope: {
        creationDate: '='
      },
      controller: FooterBrandController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function FooterBrandController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();


(function () {
    'use strict';

    angular
        .module('ghecko')
        .controller('EditProfileController', EditProfileController);


    function EditProfileController(ProfileService,Upload,$mdToast,$state) {
        var vm = this;
        var cloudObj;
        function init() {
            ProfileService.getInformation(localStorage.getItem('userId')).success(function (data) {
                vm.currentUser = data;

            });
            cloudObj = {
                url: 'https://api.cloudinary.com/v1_1/maraicah/upload',
                data: {
                    upload_preset: 'roschisp',
                    tags: 'Any',
                    context: 'photo=test'
                }
            }
        }

        init();

        vm.uploadFiles = function (files) {
            vm.photoButton = false;
            vm.uploadPhoto = true;
            console.log('cargando..');
            var file = files[0];
            cloudObj.data.file = file;
            file.upload = Upload.upload(cloudObj)
                .success(function (data, status, header, config) {
                    console.log('cargada..');
                    vm.currentUser.profilephoto = data.url;
                    ProfileService.updateInformation(vm.currentUser).success(successUpdate);
                });
        }

        vm.getInformation = function (puser) {
            vm.currentUser = puser;
        }

        vm.update = function (info) {
            ProfileService.updateInformation(info).success(successUpdate);
        }

        function successUpdate(data) {
            $mdToast.show(
                $mdToast.simple('Updated!')
                    .position('top', 'left')
                    .hideDelay(3000)
            );
            $state.go('profile',{id:vm.currentUser.id});
        }

    }
})();

(function(){
    angular
        .module('ghecko')
        .controller('CommentsController',CommentsController);
    function CommentsController($stateParams,HomeService){
        var vm = this;

        vm.idPost = $stateParams.id;
        function init(){
            HomeService.getPost(vm.idPost)
                .success(function(data){
                    buildComments(data.comments);
                });
        }
        init();

        function buildComments(comments){
            var temp = [];
            for(var i =0; i < comments.length; i++){
                var comment = comments[i];
                comment.backDate = moment(comment.date).fromNow();
                temp.push(comment);
            }
            vm.comments = temp;
        }
    }
})();

(function () {
    angular
        .module('ghecko')
        .service('AddPhotoService',AddPhotoService);
    function AddPhotoService($http,API) {
        var public_api = {
            savePost :savePost,
            getTemperature : getTemperature,
            getPost: getPost
        };
        return public_api;

        function savePost(pid,ppost) {
            return $http.post(API.url+'/normal-users/'+pid+'/posts',ppost);

        }

        function getPost(idUser,idPost){
            return $http.get(API.url+'/normal-users/'+idUser+'/posts/'+idPost);
        }

        function getTemperature(plat,plng) {
           return $http.get('http://api.apixu.com/v1/current.json?key=5214d3f4784244c6a1451147160911&q='+plat+' '+plng);

        }
    }
})();

(function () {
    'use strict';

    angular
        .module('ghecko')
        .controller('AddPhotoController', AddPhotoController);


    function AddPhotoController(NgMap, Upload, AddPhotoService, $state,$mdToast,$stateParams,ProfileService) {

        var vm = this;
        var cloudObj;

        function init() {
            //Show and hide components init
            vm.temperature = false;
            vm.photoButton = true;
            vm.uploadPhoto = false;
            vm.currentFilter = 'nofilter';
            vm.addDisabled = true;
            vm.tag = $stateParams.tag


            navigator.geolocation.getCurrentPosition(showPosition);
            NgMap.getMap().then(function (map) {
                vm.map = map;
            });
            cloudObj = {
                url: 'https://api.cloudinary.com/v1_1/maraicah/upload',
                data: {
                    upload_preset: 'roschisp',
                    tags: 'Any',
                    context: 'photo=test'
                }
            }
        }

        init();

        vm.setFilter = function (filter) {
            vm.currentFilter = filter;
        }
        function showPosition(position) {
            vm.lat = position.coords.latitude;
            vm.lng = position.coords.longitude;
            var latlng = new google.maps.LatLng(vm.lat, vm.lng);
            vm.map.setCenter(latlng);
            AddPhotoService.getTemperature(vm.lat, vm.lng)
                .success(function (data) {
                    vm.temperature = true;
                    vm.currentTemp = data.current.temp_c;
                    vm.location = data.location.name;
                })

        }


        vm.uploadFiles = function (files) {
            vm.photoButton = false;
            vm.uploadPhoto = true;
            var file = files[0];
            cloudObj.data.file = file;
            file.upload = Upload.upload(cloudObj)
                .success(function (data, status, header, config) {
                    vm.image = data.url;
                    vm.photoButton = true;
                    vm.uploadPhoto = false;
                    vm.addDisabled = false;
                });
        }

        vm.createPost = function () {

            var post = {
                photouser: vm.image,
                likes: 0,
                temperature: vm.currentTemp,
                location: vm.location,
                date: new Date(),
                time: vm.currentFilter,
                urlwaze: '',
                lat: vm.lat,
                lng: vm.lng,
                username: localStorage.getItem('username'),
                photo: localStorage.getItem('profilephoto'),
                userlikes: [],
                tag: vm.tag,
                comments: []
            }
            var idUser = localStorage.getItem('userId');
            AddPhotoService.savePost(idUser, post)
                .success(function (data) {
                    $mdToast.show(
                        $mdToast.simple('Uploaded photo!')
                            .position('top', 'left')
                            .hideDelay(3000)
                    );
                    $state.go('profile', {id: localStorage.getItem('userId')});

                })
            countPost();

        }

        function countPost() {
            ProfileService.getInformation(localStorage.getItem('userId'))
                .success(function (user) {
                    user.cantpublicaciones = user.cantpublicaciones + 1;
                    ProfileService.updateInformation(user)
                        .success(function () {


                        })


                })

        }

    }


})();

